import { defineStore } from "pinia";
export const useContent = defineStore("Films", {
  state: () => ({
    films: [],
    filmStatus: "Просмотрено",
  }),
  getters: {
    setFilms() {
      let result = null;
      result = this.films.filter((film) => film.status == this.filmStatus);
      return result;
    },
  },
  actions: {
    async getFilms() {
      try {
        let response = await fetch("https://1d95bdc879b5963a.mokky.dev/films", {
          method: "GET",
          headers: { "content-type": "application/json" },
        });
        let result = await response.json();
        this.films = result;
      } catch (error) {
        console.log(error);
      }
    },
    getFiltredFilms(status) {
      this.filmStatus = status;
    },
    returnFilm(id) {
      return this.films.filter((film) => film.id == id);
    },
  },
});
